/* SPYRIDON CSS */
.a{
  color: white !important;
  
}
 
.NavBar {
  color: white !important;
}

.nav {
  width: 100%;
  float: left;
  padding: 0;
  list-style: none; 
}

.nav li {
  float: left; }

.topnav a:hover {
    color: rgb(207, 204, 23);
    text-decoration: none;
  }
  
.topnav a {
    float: center;
    color: #f2f2f2;
    text-align: center;
    padding: 0 1rem;
    margin: 0 1rem;
    text-decoration: none;
    font-size: 3rem;
}

.text-center {
  font-family: 'Waiting for the Sunrise', cursive;
  font-size: 2rem;
  margin-top: 2rem;
}




/* FORM CSS */


form input,
form button {
  padding: 0.5rem;
  font-size: 3rem;
  font-family: 'Waiting for the Sunrise', cursive;
  background: rgba(255, 255, 255, 0);
  color: white;
  border: none;
}

form input {
  width: 20rem;
}


form button {
    padding: 1.5rem 0 1.5rem 0;
    font-size: 2rem;
  color: rgb(207, 204, 23);
  cursor: pointer;
  transition: all 0.3s ease;
  width: 10rem;

}

form button:hover {
  transform: scale(1.3);

}


/*CUSTOM SELECTOR */
.filter-todo {
    font-size: 2rem;
    width: 10rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 0 1rem 0 0.5rem;
    border-radius: 8px;
  }

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;
    color: rgb(207, 204, 23);
    cursor: pointer;
    background: rgb(105, 104, 104);
  }

  
  /* Custom Select */
.select {
    margin: 0 1rem 0 1rem;
    position: relative;
    color: rgb(207, 204, 23);
    font-family: 'Waiting for the Sunrise', cursive;
    padding: 1.5rem 0 1.5rem 0;
  }
 
  
  /* Filter-Icon */
.select::after {
    content: "\f0b0";
    font-family: FontAwesome;
    position: absolute;
    cursor: pointer;
    font-size: 2rem;
    transition: all 0.3s ease;
    right: 0.6rem;
    
  }
  
  
  /* Transition */
  
  .select:hover::after {
    transform: scale(1.3);
  }

.todo-container {
    display: flex;
    justify-content: center;
    align-items: top;
    overflow: auto;
    height: 35rem;
  }
  
  .todo-list {
    width: 90%;
    list-style: none;
    font-family: 'Waiting for the Sunrise', cursive;
  }
.todo {
    margin: 0.5rem;
    /*background: white;*/
    color: white;
    font-size: 3rem;
    justify-content: space-between;
    align-items: center;
    transition: all 1s ease;
  }

.display {
    display: none;

}



.todoBtns-flex {
  float: right;
}
  
  
  
  .trash-btn,
  .complete-btn {
    border: none;
    padding: 0.5rem;
    cursor: pointer;
    font-size: 2rem;
    transition: all 0.3s ease;
    justify-content: right;
  }

  .trash-btn {
    color: rgb(207, 204, 23);
    background: rgba(255, 255, 255, 0);
  }

  .trash-btn:hover {
    transform: scale(1.3);
  }

  .complete-btn {
    color: rgb(207, 204, 23);
    background: rgba(255, 255, 255, 0);
  }

  .complete-btn:hover {
    transform: scale(1.3);
  }
  
  
  
  
  .fall {
    transform: translateY(10rem) rotateZ(20deg);
    opacity: 0;
  }
  
  .completed {
    text-decoration: line-through;
    -webkit-text-decoration-color: rgb(207, 204, 23);
            text-decoration-color: rgb(207, 204, 23);
    opacity: 0.6;

  }


